class Counter2 extends Component {
  constructor(props) {
    super(props)
    this.state = {
      typeList: []
    }
  }
  componentDidMount() {
    this.fnPageInit()
  }

  fnPageInit() {
    this.setState({
      typeList: setData()
    })
  }

  clickHandle() {
    if ('undefined' != typeof wx && wx.getSystemInfoSync) {
      wx.navigateTo({
        url: '../typelist/index?id=1'
      })
    } else {
      location.href = 'typelist.html?id=1'
    }
  }

  render() {
    let { typeList } = this.state
    return (
      <div className="counter">
        <div onClick={this.clickHandle}></div>
        <ul className="type_list">
          {typeList &&
            typeList.length > 0 &&
            typeList.map((item, i) => {
              return (
                <li key={i} className="type_item">
                  <div className="lebal">{item.name}</div>
                  <div className="img_box"><img src={item.img} /></div>
                </li>
              )
            })}
        </ul>
      </div>
    )
  }
}


function Counter(r) {
  const [count, setCount] = useState(0)
  const [list, setList] = useState([])
  console.log('list', list)
  console.log('props', r)

  function sett() {
    // [count, setCount] = useState(0)
    // console.log('count', count)
    setCount(count + 1)
  }

  return (
    <div>
      <button onClick={() => setCount(count - 1)}>-</button>
      <span>{count}</span>
      <button onClick={sett}>+</button>
      {/* <button onClick={() => setCount(count + 1)}>+</button> */}
      <div onClick={clickHandle}></div>
      <br />
      {/* <div onClick={setData}></div> */}
    </div>
  )
}

function clickHandle() {
    if ('undefined' != typeof wx && wx.getSystemInfoSync) {
      wx.navigateTo({
        url: '../log/index?id=1'
      })
    } else {
      location.href = 'log.html'
    }
  }

export default Counter2
